export default class SearchByKeywords {
  constructor(keyWords, token) {
    this.APIKey = 'AIzaSyAzCCgEe-fAnCvOGOlRZYBriIeNJ2-C3iM';
    // this.APIKey = 'AIzaSyCEItDcKLpIxSMFXDNXVd7mikJXl9IyowU';
    // this.APIKey = 'AIzaSyAmH3JW546-l8w8cZgq-vrDn0F-foi0iTw';
    this.keyWords = keyWords;
    this.token = token;
  }


  async makeRequest() {
    let requestIDs;

    if (this.token) {
      requestIDs = await fetch(`https://www.googleapis.com/youtube/v3/search?key=${this.APIKey}&type=video&part=id&maxResults=15&pageToken=${this.token}&q=${this.keyWords}`);
    } else {
      requestIDs = await fetch(`https://www.googleapis.com/youtube/v3/search?key=${this.APIKey}&type=video&part=id&maxResults=15&q=${this.keyWords}`);
    }

    const responceIDs = await requestIDs.json();

    if (responceIDs.nextPageToken) {
      this.token = responceIDs.nextPageToken;
    }

    const listOfIds = responceIDs.items.map(item => item.id.videoId).join(',');

    const requestlink = `https://www.googleapis.com/youtube/v3/videos?key=${this.APIKey}&id=${listOfIds}&part=snippet,statistics`;

    return requestlink;
  }
}
