import ClipUnit from './ClipUnit';

export default class RequestResults {
  constructor(requestLink) {
    this.requestLink = requestLink;
  }


  static extractResults(responceData) {
    return responceData.items.map((clip) => {
      const {
        id,
        snippet: {
          publishedAt = ' ',
          title = ' ',
          description = ' ',
          thumbnails: { medium: { url } } = ' ',
          channelTitle = ' ',
        },
        statistics: { viewCount } = { viewCount: ' ' },
      } = clip;
      const link = `https://www.youtube.com/watch?v=${id}`;
      return new ClipUnit(title, url, channelTitle, publishedAt, viewCount, description, link);
    });
  }


  async getResults() {
    const request = this.requestLink;

    const responce = await fetch(request);
    const data = await responce.json();

    return RequestResults.extractResults(data);
  }
}
