export default class ClipUnit {
  constructor(title, picture, channelName, uploadDate, viewCount, description, link) {
    this.title = title;
    this.picture = picture;
    this.description = description;
    this.channelName = channelName;
    this.uploadDate = uploadDate;
    this.viewCount = viewCount;
    this.link = link;
  }
}
