export default class StartView {
  constructor() {
    this.placeholder = 'Hit me!';
  }

  render() {
    // create main container
    const mainContainer = document.createElement('main');
    mainContainer.className = 'main__container';
    document.body.appendChild(mainContainer);

    // create searchbox
    const searchBoxContainer = document.createElement('section');
    searchBoxContainer.className = 'search__box__container';
    mainContainer.appendChild(searchBoxContainer);

    const searchInput = document.createElement('input');
    searchInput.className = 'search__box';
    searchInput.setAttribute('type', 'text');
    searchInput.setAttribute('placeholder', `${this.placeholder}`);
    searchBoxContainer.appendChild(searchInput);

    // create section for clips
    const clipSection = document.createElement('section');
    clipSection.className = 'clip__section';
    mainContainer.appendChild(clipSection);

    const sliderBox = document.createElement('div');
    sliderBox.className = 'slider__box';
    clipSection.appendChild(sliderBox);

    // create section with navigation buttons
    const navContainer = document.createElement('section');
    navContainer.className = 'nav__container';
    mainContainer.appendChild(navContainer);
    navContainer.classList.toggle('hidden');

    const prevPopUp = document.createElement('div');
    prevPopUp.className = 'prev__pop';
    navContainer.appendChild(prevPopUp);
    prevPopUp.classList.add('hidden');

    const prevButt = document.createElement('button');
    prevButt.className = 'prev__button';
    prevButt.innerText = '<';
    navContainer.appendChild(prevButt);
    prevButt.classList.toggle('hidden');

    const currButt = document.createElement('button');
    currButt.className = 'curr__button';
    currButt.innerText = '1';
    navContainer.appendChild(currButt);

    const nextButt = document.createElement('button');
    nextButt.className = 'next__button';
    nextButt.innerText = '>';
    navContainer.appendChild(nextButt);

    const nextPopUp = document.createElement('div');
    nextPopUp.className = 'next__pop';
    navContainer.appendChild(nextPopUp);
    nextPopUp.classList.add('hidden');
  }
}
