export default class ClipUnitView {
  constructor(unit) {
    this.unit = unit;
  }

  render() {
    const clipContainer = document.createElement('div');
    clipContainer.className = 'clip__container';

    // add title
    const clipTitle = document.createElement('a');
    clipTitle.className = 'clip__title';
    clipTitle.setAttribute('href', `${this.unit.link}`);
    clipTitle.setAttribute('target', '_blank');
    clipContainer.appendChild(clipTitle);
    clipTitle.textContent = `${this.unit.title}`;

    // add picture
    const clipPictureContainer = document.createElement('div');
    clipPictureContainer.className = 'clip__picture__container';
    const clipPicture = document.createElement('img');
    clipPicture.className = 'clip__picture';
    clipPicture.setAttribute('src', `${this.unit.picture}`);
    clipPicture.setAttribute('alt', 'not found');
    clipContainer.appendChild(clipPictureContainer);
    clipPictureContainer.appendChild(clipPicture);

    // add info block
    const names = [
      ['channel__name', `${this.unit.channelName}`],
      ['upload__date', `${this.unit.uploadDate}`],
      ['view__count', `${this.unit.viewCount}`],
    ];
    names.forEach((element) => {
      const clipInfo = document.createElement('div');
      clipInfo.className = 'clip__info';

      const clipInfoIcon = document.createElement('i');
      clipInfoIcon.className = `clip__info__icon ${element[0]}`;

      const clipInfoText = document.createElement('p');
      clipInfoText.className = 'clip__info__text';
      clipInfoText.textContent = `${element[1]}`;

      clipContainer.appendChild(clipInfo);
      clipInfo.appendChild(clipInfoIcon);
      clipInfo.appendChild(clipInfoText);
    });

    // add description
    const clipDescription = document.createElement('p');
    clipDescription.className = 'clip__description';
    clipDescription.textContent = `${this.unit.description}`;
    clipContainer.appendChild(clipDescription);

    return clipContainer;
  }
}
