import ResModel from '../models/Results';
import SearchByKeywords from '../models/SearchByKeywords';

import StartView from '../views/StartView';
import ClipUnitView from '../views/ClipUnitView';

// import ScrollController from './ScrollController';

export default class YoutubeApp {
  constructor() {
    this.requestLink = '';
    this.token = '';
    this.keywords = '';
  }

  async getResults() {
    // console.log(this.token);
    const searchRequest = new SearchByKeywords(this.keywords, this.token);

    this.requestLink = await searchRequest.makeRequest();
    // console.log(searchRequest.token);
    this.token = await searchRequest.token;
    // console.log(this.token);

    const model = new ResModel(this.requestLink);
    const clipsData = await model.getResults();

    const sliderBox = document.querySelector('.slider__box');

    clipsData.forEach((element) => {
      const videoUnit = new ClipUnitView(element);
      sliderBox.appendChild(videoUnit.render());
    });

    const navContainer = document.querySelector('.nav__container');
    if (navContainer.classList.contains('hidden')) {
      navContainer.classList.toggle('hidden');
    }
  }

  async start() {
    const container = new StartView();
    container.render();

    const sliderBox = document.querySelector('.slider__box');
    const hitMe = document.querySelector('.search__box');
    const prev = document.querySelector('.prev__button');
    const curr = document.querySelector('.curr__button');
    const next = document.querySelector('.next__button');

    const page = {
      currPage: 1,
      unitWidth: 320,
      clipsOnPage: 4,
    };

    // search
    hitMe.addEventListener('change', (event) => {
      this.keywords = event.target.value.split(' ').join(',');
      while (sliderBox.lastChild) {
        sliderBox.removeChild(sliderBox.lastChild);
      }
      this.getResults();
      page.currPage = 1;
    });

    // resize
    function setClipsOnPage() {
      const maxWidth = document.body.clientWidth;
      return Math.min(Math.floor(maxWidth / page.unitWidth), 4);
    }

    // document.body.addEventListener('resize', () => {
    //   console.log(page.currPage);
    //   const numberOfClips = (page.currPage - 1) * page.clipsOnPage;
    //   page.clipsOnPage = setClipsOnPage();
    //   page.currPage = Math.floor(numberOfClips / page.clipsOnPage);
    //   console.log(page.currPage);
    //   curr.textContent = `${page.currPage}`;
    //   sliderBox.scrollLeft = (page.unitWidth * (page.currPage - 1) * page.clipsOnPage);
    // });

    // scroll
    function turn(route) {
      page.clipsOnPage = setClipsOnPage();

      if (route === 'left' && page.currPage > 1) {
        page.currPage -= 1;
      } else if (route === 'right') {
        page.currPage += 1;
      }

      sliderBox.scrollLeft = (page.unitWidth * (page.currPage - 1) * page.clipsOnPage);

      if (page.currPage === 1) {
        prev.classList.add('hidden');
      } else prev.classList.remove('hidden');

      curr.textContent = `${page.currPage}`;
    }

    // scroll with buttons
    const prevPopUp = document.querySelector('.prev__pop');
    const nextPopUp = document.querySelector('.next__pop');

    prev.addEventListener('pointerdown', () => {
      prevPopUp.textContent = `${page.currPage - 1}`;
      prevPopUp.classList.remove('hidden');
    });

    next.addEventListener('pointerdown', () => {
      nextPopUp.textContent = `${page.currPage + 1}`;
      nextPopUp.classList.remove('hidden');
    });

    prev.addEventListener('pointerup', () => {
      prevPopUp.classList.add('hidden');
    });

    next.addEventListener('pointerup', () => {
      nextPopUp.classList.add('hidden');
    });


    prev.addEventListener('click', () => {
      turn('left');
    });

    next.addEventListener('click', () => {
      turn('right');

      const numberOfClips = page.unitWidth * (page.currPage - 1) * page.clipsOnPage;
      const needMore = document.querySelectorAll('.clip__container').length - 5;
      if (numberOfClips > needMore) {
        this.getResults();
      }
    });

    // swipe
    let isDown = false;
    let startX;
    let currentX;

    sliderBox.addEventListener('pointerdown', (event) => {
      isDown = true;
      sliderBox.classList.add('grab');
      startX = event.pageX - sliderBox.offsetLeft;
    });

    sliderBox.addEventListener('pointerleave', () => {
      isDown = false;
      sliderBox.classList.remove('grab');
    });

    sliderBox.addEventListener('pointerup', () => {
      isDown = false;
      sliderBox.classList.remove('grab');
      turn(currentX > startX ? 'left' : 'right');
    });

    sliderBox.addEventListener('pointermove', (event) => {
      if (isDown) {
        event.preventDefault();
        currentX = event.pageX - sliderBox.offsetLeft;
      }
    });
  }
}
